.App {
  border: 10px double rgb(0, 0, 0);
  border-bottom: none;
  text-align: center;
  /* height: 90vh; */
}

header {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 80px;
  height: 290px;
}

header h1 {
  font-family: "Lobster", cursive;
  font-size: 40px;
  letter-spacing: 2px;
  color: rgb(0, 37, 70);
}
header div {
  width: 500px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  flex-wrap: wrap;
}
header div input {
  width: 340px;
  height: 30px;
  border-radius: 4px;
  border: none;
  border-bottom: 1px solid rgb(173, 173, 173);
  margin-top: 30px;
  position: static;
  font-size: 20px;
  margin-left: 70px;
}
header input:focus {
  border: none;
  border-bottom: 1px solid rgb(173, 173, 173);
  outline: none;
}
header button {
  width: 70px;
  height: 50px;
  background-color: rgb(0, 0, 56);
  color: rgb(214, 206, 250);
  border: none;
  font-weight: 800;
}
.btns {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-bottom: 70px;
  flex-wrap: wrap;
}
.btns button {
  width: 100px;
  color: rgb(0, 0, 56);
  border-color: rgb(191, 151, 255);
  border-radius: 4px;
  background-color: rgb(214, 206, 250);
  border-radius: 5px 0 5px 0;
  height: 40px;
}
.photoes {
  display: flex;
  justify-content: center;
  gap: 40px;
  flex-wrap: wrap;
  padding: 40px;
}
.photoes img {
  width: 250px;
  height: 250px;
  box-shadow: 15px 10px 20px rgb(148, 148, 148);
}
@media only screen and (max-width: 600px) {
  .photoes img {
    width: 100px;
    height: 100px;
  }
  header div {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
  }
  header div input {
    width: 200px;
    margin-left: 0;
    margin-bottom: 10px;
  }
}
.loading {
  display: flex;
  justify-content: center;
}
